body {
  margin: 0;
  padding:0;
  height: 100%;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-image: url('./assets/image/weather--02.jpg'); 
  background-size: cover;
  background-position: bottom;
  
  transition: 0.4s ease-out;
  
}

.App.warm{
  background-image: url('./assets/image/weather--01.jpg');
}

.App.warm.night{
  background-image: url('./assets/image/weather--08.jpg');
}
.App.night{
  background-image: url('./assets/image/weather--06.jpg');
}

main{
    min-height: 100vh;
    
    background-image: linear-gradient(to bottom, rgba(0,0,0,0.3), rgba(0,0,0,1) );
    padding: 25px;
}
  
  .searchBox{
    width: 100%;
    margin: 0 auto; 
  }
  
  .searchBox .searchBar{
    display: block;
    width: 90% ;
    padding: 15px;
  
    appearance: none;
    background: none;
    border: none;
    outline: none;
  
    background-color: rgba(255,255,255,0.4);
    border-radius: 30px; 
  
    box-shadow: 0px 1px 15px rgba(0,0,0,0.5);
  
    /* border-bottom: 2px solid white; */
    color: white;
    text-align: center;
  
    transition:  0.4s ease-in-out; 
  }
  
  .searchBox .searchBar:hover{
    background-color:rgba(255,255,255,0.9) ;
    color: black;
  }
  
  
  .locationBox  .location{
    margin-top: 25px;
    color: white;
    font-size: 40px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 5px rgba(0,0,0,0.3);
    
  }
  
  .locationBox  .date{
    color: white;
    font-size: 20px;
    font-weight: 300;
    text-align: center;
    text-shadow: 3px 5px rgba(0,0,0,0.3);
    font-style: italic;
  }
  


  
.weatherBox{
margin: 25px auto;
  display: flex;
  flex-direction: column;
align-content: center;
  background-color: rgba(255  , 255, 255, 0.2);
  border-radius: 30px;
}

.weatherBox .icon{
  width: 80px;
  margin: 25px auto;
}
.weatherBox .temp{
  margin-top: -30px;
  color: white;
  font-size: 92px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 5px rgba(0,0,0,0.3);
}
.weatherBox .weather{
  color: white;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  text-shadow: 3px 5px rgba(0,0,0,0.3);
  font-style: italic;
  margin-bottom: 25px;
}
  


@media (min-width : 500px){
  .weatherBox{
    width: 500px;
  }
  .searchBox .searchBar{
    margin:  0 auto;
    width: 500px;
  }
  .App {
    background-image: url('./assets/image/weather-full-09.jpg');
  }
  .App.warm{
    background-image: url('./assets/image/weather-full-08.jpg') ;
  }
  .App.night{
    background-image: url('./assets/image/weather-full-11.jpg');
  }

  .App.warm.night{
    background-image: url('./assets/image/weather-full-10.jpg');
  }
}